a {
  text-decoration: none;
}

.carousel-item-height {
  height: 600px;
  background-size: cover;
  background-position: center center;
}

.shop-info-pic {
  height: 400px;
  background-size: cover;
  background-position: center center;
}

.title-border {
  position: relative;
}

.title-border::after {
  content: '';
  position: absolute;
  width: 100px;
  height: 5px;
  background-color: #D79771;
  left: 50%;
  top: 150%;
  transform: translateX(-50%);
}

.btn-hover {
  transition: all .3s;
}

.btn-hover:hover {
  letter-spacing: 3px;
}

.card-img-height {
  height: 300px;
  background-size: cover;
  background-position: center center;
}

.card-shadow {
  transition: all .3s;
}

.card-shadow:hover {
  box-shadow: 1px 3px 6px #BBB;
}

.discount-card {
  background-size: cover;
  background-position: center center;
  background-color: rgba(0, 0, 0, 0.6);
  background-blend-mode: multiply;
}

.custer-comment {
  background-color: rgba(0, 0, 0, 0.6);
  background-blend-mode: multiply;
  background-size: cover;
  background-position: center center;
}

.card-img-circle {
  width: 150px;
  height: 150px;
  border: 1px solid transparent;
  border-radius: 50%;
  overflow: hidden;
  background-size: cover;
  background-position: center center;
}

.card-height-hover {
  transition: all .3s;
}

.card-height-hover:hover {
  transform: translateY(-20px);
}

.booking-form {
  background-color: rgba(255, 255, 255, 0.8);
  background-blend-mode: screen;
  background-size: cover;
  background-position: center center;
}

.form-check-input:checked {
  background-color: #D79771;
  border-color: #D79771;
}

.form-check-input:focus,
.form-control:focus {
  box-shadow: 0 0 0 0.25rem #FFEBC9;
  border-color: transparent;
}

.shop-img {
  width: 100%;
  height: 300px;
  background-size: cover;
  background-position: center center;
}
